<template>
  <div class="calculator">
    <h1>{{ text.title }}</h1>
    <div class="ui__subheader">{{ text.subheader }}</div>
    <div class="calculatorSections">
      <div class="calculatorSectionLeft">
        <div class="calculatorSection type">
          <div class="ui__formTitle">
            {{ text.type }}
          </div>
          <div>
            <label v-for="type in formType.items">
            <input :id="type.txt" :value="type.val" type="radio" name="type.txt" autocomplete="off" v-model="formType.checked" v-bind="type.val" />{{ type.txt }}
            </label>
          </div>
        </div>
        <div class="calculatorSection zone">
          <div class="ui__formTitle">
            {{ text.zone }}
          </div>
            <label v-for="zone in formZone.items">
            <input :id="zone.txt" :value="zone.val" type="radio" name="zone.txt" autocomplete="off" v-model="formZone.checked" v-bind="zone.val" />{{ zone.txt }}
            </label>
          </div>
      </div>
      <div class="calculatorSectionRight">
        <div class="calculatorSection areaSlider">
          <div class="ui__formTitle">
            {{ text.area }}&nbsp;&nbsp;{{ sliders.areaSlider.value }}&nbsp;м²
          </div>
          <div>
            <vue-slider ref="slider1" v-bind="sliders.areaSlider" v-model="sliders.areaSlider.value"></vue-slider>
          </div>
        </div>
        <div class="calculatorSection peopleSlider">
          <div class="ui__formTitle">
            {{ text.people }}&nbsp;&nbsp;{{ sliders.peopleSlider.value }}&nbsp;чел.
          </div>
          <div>
            <vue-slider ref="slider1" v-bind="sliders.peopleSlider" v-model="sliders.peopleSlider.value"></vue-slider>
          </div>
        </div>
      </div>
      <div class="calculatorSectionSummary">
          <div class="summaryValue">
            <div class="">
              {{ text.previousNumber }}
              <div class="ui__huge">
              {{ previousPrice }}&nbsp;руб.
              </div>
            </div>
          </div>
          <div class="summaryValue">
            <div class="">
              {{ text.currentNumber }}
              <div class="ui__huge">
              {{ currentPrice }}&nbsp;руб.
              </div>
            </div>
          </div>
          <div class="summaryValue">
            <div class="">
              {{ text.marginNumber }}
              <div class="ui__huge">
              {{ priceMargin }}&nbsp;руб.
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import vueSlider from 'vue-slider-component'

export default {
  name: 'Calculator',
  components: {
    vueSlider
  },
  data () {
    return {
      text: {
        title: 'Мусорный калькулятор',
        subheader: 'Считаем, насколько больше мы станем платить за мусор в 2019 году',
        type: 'Тип жилья',
        zone: 'Зона',
        area: 'Площадь жилья',
        people: 'Прописано',
        registered: 'Зарегистрировано',
        previousNumber: 'Было до 2019',
        currentNumber: 'Стало с 2019',
        marginNumber: 'Разница'
      },
      formType: {
          items : [{txt: 'Многоквартирный дом', val: 3},{txt: 'Частный дом', val: 2.8}],
          checked:'3'
      },
      formZone: {
          items : [{txt: 'Екатеринбург', val: 1},{txt: 'Северная зона', val: 0.8},{txt: 'Западная зона', val: 0.75},{txt: 'Восточная зона', val: 0.7}],
          checked:'1'
      },
      sliders: {
        areaSlider: {
          value: 45,
          width: 250,
          height: 12,
          dotSize: 20,
          min: 1,
          max: 200,
          interval: 0.1,
          useKeyboard: true,
          disabled: false,
          show: true,
          speed: 0.3,
          reverse: false,
          tooltip: 'never',
          piecewise: false,
          formatter: "{value}"

        },
        peopleSlider: {
          value: 1,
          width: 250,
          height: 12,
          dotSize: 20,
          min: 1,
          max: 20,
          useKeyboard: true,
          disabled: false,
          show: true,
          speed: 0.3,
          reverse: false,
          tooltip: 'never',
          piecewise: false,
          formatter: "{value}"
        }
      }
    }
  },
  computed: {
    previousPrice: function () {
      return Math.round((this.sliders.areaSlider.value * this.formType.checked * this.formZone.checked) * 100) / 100
    },
    currentPrice: function () {
      return Math.round((this.sliders.areaSlider.value * this.sliders.peopleSlider.value * this.formType.checked * this.formZone.checked) * 100) / 100
    },
    priceMargin: function () {
      return Math.round((this.currentPrice - this.previousPrice) * 100)/100
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



